<script>
import "./assets/contact-style.css"
import "./assets/style.css";
import $ from "jquery";

export default {
  created() {

    let language = this.$route.query.lang
    if (language == null) {
      this.lang = "az";
    } else {
      this.lang = language;
    }
    $(window).scroll(function (e) {
      if (typeof window === 'undefined') return
      const top = window.pageYOffset || e.target.scrollTop || 0
      this.fab = top > 20
      if (this.fab) {
        $(".go-back").show(500)
      } else {
        $(".go-back").hide(500)
      }
    })
  },
  data() {
    return {
      lang: this.$route.query.lang,
      fab: false
    }
  },
  methods: {
    hardRefresh(link) {
      this.$router.push(link)
      location.reload();
    },
    showPage() {
      document.getElementById("loader").style.display = "none";
      document.getElementById("app").style.display = "block";
    },
    toTop() {
      $('html, body').animate({scrollTop: 0}, 'slow', function () {
      })
    }
  },
  mounted() {
    setTimeout(function () { this.showPage() }.bind(this), 3000)
    window.addEventListener("load", () => {
      let item = localStorage.getItem("lastCollapse");
      if (item) {
        let generated="a[name="+item+"]";
        document.getElementById(item).classList.toggle("show")
        document.querySelector(generated).setAttribute("aria-expanded", "true")
        // .setAttribute("aria-expanded", "true")
      } else {
        let elements = document
            .getElementById("body")
            .getElementsByClassName("collapse");
        elements[0].classList.toggle("show");

        document
            .getElementById("body")
            .getElementsByClassName("nav-link")[0]
            .setAttribute("aria-expanded", "true")
      }
    });

  },


  name: "App",
  components: {},

};
</script>

