<template>
  <div class="super-parent">

    <nav class="" style="margin-top: 160px" id="body">
      <div class="container">
        <div class="col text-center" style="margin-bottom: 50px">
          <h1

              style="
         color: #a07c37;;
          font-family: 'Montserrat', sans-serif;
          font-weight: bold;
        "
          >
            {{ menuText }}
          </h1>

        </div>
      </div>

        <div class="row nav-tabs" style="justify-content: center; margin-bottom: 50px;margin-right: 0px;margin-left: 0px">
          <div v-for="(category,index) in mainCategory.categories"  class="category"
              :key="category.id">
            <Category :collapsId="category.collapsId" :name="category.name" :index="index"/>
          </div>

        <div id="accordion" style="margin-top: 50px">
          <div
              v-for="category in mainCategory.categories"
              :key="category.id"
              class="collapse"
              :id="category.collapsId"
              data-parent="#accordion"
          >
            <h5
                style="
              color: #a07c37;
              font-family: Montserrat, sans-serif;
              font-weight: bold;
              margin-bottom:10px;
            "
                v-if="category.meals"
            >
              {{ category.name }} <span>
              <img class="category-logo" :src="require('@/assets/img/logos/'+category.logo)"/>
            </span>
            </h5>
            <div class="row" v-if="category.meals">
              <Product
                  v-for="meal in category.meals"
                  :key="meal.id"
                  :id="category.collapsId"
                  :price="meal.price"
                  :content="meal.content"
                  :imgPath="meal.imgPath"
                  :name="meal.name"
                  :util="meal.util"
                  :read-more-text="readMoreText"
                  :read-less-text="readLessText"
                  :is-drink="false"
              />
            </div>
            <!-- İçkilər üçün -->
            <div class="row" v-if="category.drinks">
              <ProductDrink
                  v-for="drink in category.drinks"
                  :key="drink.id"
                  :id="category.collapsId"
                  :price="drink.price"
                  :content="drink.content"
                  :imgPath="drink.logo"
                  :name="drink.name"
                  :util="drink.util"
                  :is-drink="true"
                  :sub-products="drink.subProducts"
              />
            </div>
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>
<script>
import Product from "./Product.vue";
import ProductDrink from "@/components/ProductDrink";
import Category from "./Category.vue";
import categories from "./data.json"

export default {
  props: {},
  components: {
    ProductDrink,
    Product,
    Category,
  },
  created() {

    let language = this.$route.query.lang
    if (language == null) {
      this.lang = "az";
    } else {
      this.lang = language;
    }
    if (this.lang == 'az') {
      this.readMoreText = "daha çox"
      this.readLessText = 'daha az'
      this.menuText = 'Menyu'
      this.mainCategory = categories.categoriesAZ;
    } else if (this.lang == 'en') {
      this.readMoreText = 'read more'
      this.readLessText = 'read less'
      this.menuText = "Menu"
      this.mainCategory = categories.categoriesEN
    } else {
      this.readMoreText = 'читать больше'
      this.readLessText = 'читать меньше'
      this.menuText = 'Меню'
      this.mainCategory = categories.categoriesRU;
    }

  },


  data() {
    return {
      lang: "az",
      mainCategory: {},
      readMoreText: "",
      readLessText: "",
      menuText: "",
    };
  },
};
</script>
<style>
</style>